import Vue from 'vue'
import * as types from './mutation-types'
//
const commModule = {
    state: {
      selectedTab: 'index',
      footerVisible: false,
    },

    //state storemutations
    mutations: {
      [types.TOGGLE_FOOTER] (state) {
        state.footerVisible = !state.footerVisible
      },
      [types.SELECT_TAB](state, val){
        state.selectedTab = val;
      }
    },
    actions: {
     
    },
    // 
    getters: {
        
    }
}

export default commModule;


